#accueil-link-mobile:hover,
#projets-link-mobile:hover,
#cv-link-mobile:hover,
#contact-link-mobile:hover,
#accueil-link-ordi:hover,
#projets-link-ordi:hover,
#cv-link-ordi:hover,
#contact-link-ordi:hover {
  color: #dd7893; /* Couleur du texte au survol et pour le bouton actif */
  font-weight: bold;
}

#accueil-link-mobile.active,
#projets-link-mobile.active,
#cv-link-mobile.active,
#contact-link-mobile.active,
#accueil-link-ordi.active,
#projets-link-ordi.active,
#cv-link-ordi.active,
#contact-link-ordi.active {
  color: #dd7893; /* Couleur du texte au survol et pour le bouton actif */
  font-weight: bold;
}

#accueil-link-mobile,
#projets-link-mobile,
#cv-link-mobile,
#contact-link-mobile {
  display: none;
}
#accueil-link-ordi,
#projets-link-ordi,
#cv-link-ordi,
#contact-link-ordi {
  margin-left: auto;
}

@media (max-width: 768px) {
  #accueil-link-mobile,
  #projets-link-mobile,
  #cv-link-mobile,
  #contact-link-mobile {
    display: flex;
  }

  #accueil-link-ordi,
  #projets-link-ordi,
  #cv-link-ordi,
  #contact-link-ordi {
    display: none;
  }
}

@media (max-width: 360px) {
  .navbar-logo {
    display: none;
  }
  .navbar-brand {
    display: none;
  }
}

.barnav {
  display: flex;
  position: relative;
  flex-direction: row;
  align-items: center;
  padding: 0 0.5rem;
  height: 100px;
  line-height: 3.5rem;
  position: fixed;
  top: 0;
  top: 0;
  left: 0;
  right: 0;
}
.navbar-logo {
  display: "flex";
  align-items: "center";
  height: calc(3.5rem - 5%);
}

img {
  height: "60px";
  width: "35px";
  display: "block";
}

.navbar-brand {
  text-decoration: none;
  font-size: 1.5rem;
  color: inherit;
}

.navbar-menu {
  display: flex;
  flex-direction: row;
  margin: 0;
  padding: 0;
}

.navbar-link {
  color: white;
  text-decoration: none;
  display: block;
  padding: 0 0.6rem;
  transition: background-color 0.23s ease;
  cursor: pointer;
}

@media screen and (max-width: 576px) {
  .navbar-link {
    padding: 0 0.4rem;
  }
}
